$transition: .3s ease-in-out;

@media screen and (max-width: 1200px) {
  .member-fields {
    width: 82%;
    padding: 0 0 0 20px;
  }
  .edit-member .member-fields .member-switcher:before {
    left: -20px;
    right: -20px;
  }
}

@media screen and (max-width: 991px) {
  .page-menu {
    .menu-list {
      width: 100%;
      max-width: 244px;
      min-height: unset;
      padding: 18px 4px;
      display: flex;
      flex-direction: column;
      position: fixed;
      top: 0;
      left: 0;
      bottom: 0;
      z-index: 10;
      background-color: #79818C;
      opacity: 0;
      visibility: hidden;
      transform: translateX(-100%);
      pointer-events: none;
      transition: transform $transition, opacity $transition, visibility $transition;

      &.open {
        opacity: 1;
        visibility: visible;
        transform: translateX(0);
        pointer-events: auto;
      }

      &:before {
        display: none;
      }

      .item-wrap {
        flex: 1;
        max-height: 100%;
        padding: 0 17px;
        overflow: auto;
      }
    }

  }
  .toolbar-menu {
    flex-wrap: wrap;
    padding-left: 0;
  }
  .toolbar-options {
    justify-content: flex-end;
  }
  .toolbar-menu ul li {
    padding: 8px 5px;
  }
  .content-wrap {
    padding: 12px 0 0 0;
  }
  .review-item .text-wrap p {
    max-width: 565px;
  }

  .login-form {
    padding: 40px 15px 40px 0;
  }

  .search-field input {
    padding: 15px 45px 15px 13px;
    font-size: 13px;
  }
  .table-wrap table tbody td {
    font-size: 13px;
  }
  .table-intro .total p {
    padding: 10px 25px;
    font-size: 14px;

    span {
      margin-left: 5px;
      font-size: 18px;
    }
  }


  .chat-list-wrap {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 5;

    opacity: 0;
    visibility: hidden;
    transition: visibility $transition, opacity $transition;
    pointer-events: none;

    &.open {
      opacity: 1;
      visibility: visible;
      pointer-events: auto;

      + .data {
        pointer-events: none;
      }
    }
  }
  .prev-return {
    padding: 8px 0;
  }
  .prev-return a span {
    font-size: 14px;
    padding-right: 15px;
  }

  .edit-member {
    margin: 0 -20px -11px -20px;
  }

  .member-fields {
    width: 100%;
    padding: 0 20px;
  }
  .edit-member .member-info:before {
    display: none;
  }

  .member-info {
    width: 100%;
    padding-bottom: 10px;
  }
}

@media (max-width: 767px) {
  .user-data p {
    display: none;
  }
  .user-data .info {
    padding-left: 0;
    border-left: none;
  }
  .user-data .info button {
    margin-left: 10px;
  }
  .login-form {
    width: 100%;
    min-height: unset;
    padding: 40px 0;
  }
  .login-bg {
    width: 100%;

    &:before {
      content: '';
      display: block;
      padding-bottom: 80%;
    }

    .bg-wrap {
      width: 101vw;
      left: 50%;
      transform: translateX(-50%);
    }
  }
  .table-intro {
    flex-direction: column;
  }
  .table-intro .total {
    padding: 10px 0 0 0;

    p {
      justify-content: center;
    }
  }
  .table-wrap .action .main-btn {
    padding: 8px 16px;
    font-size: 11px;
  }
  .table-wrap table tbody td {
    padding: 6px 6px 6px 0;
  }
  .table-wrap table thead td {
    padding: 0 6px 8px 0;
  }
  .toolbar-options {
    width: 100%;
    flex-direction: column;
    padding-top: 0;

    .item {
      width: 100%;
      padding: 10px 0 0 0;

      .main-btn {
        width: 100%;
      }

      &.left-border {
        margin: 10px 0 0 0;
        border-top: 1px solid rgba(121, 129, 140, 0.2);
        border-left: none;
      }
    }

    .select-field select,
    .select-date input {
      max-width: unset;
    }
  }
  .table-intro .select-date {
    width: 100%;
    margin-top: 15px;

    input {
      max-width: unset;
    }
  }
  .tabs-wrap .table-intro .tabs-menu {
    width: 100%;
    padding-right: 0;
    border-bottom: 1px solid #C2CEDD;
  }
  .tabs-wrap .table-intro {
    border-bottom: none;
  }
}

@media (max-width: 575px) {
  .main-links {
    width: 100%;

    ul {
      justify-content: space-around;

      li {
        padding-right: 15px;

        &:last-child {
          padding-right: 0;
        }
      }
    }
  }
  .page-intro .user-data {
    display: none;
  }
  .faq-item {
    flex-direction: column;
  }
  .faq-item .control {
    padding: 12px 0 0 0;

    .main-btn {
      width: 100%;
    }
  }

  .message-text .content {
    max-width: 100%;
    min-width: unset;
    padding: 10px 7px;
  }
  .active-chat .chat-control {
    flex-wrap: wrap;
  }
  .active-chat .chat-control .btn {
    width: 100%;
    justify-content: center;
    padding-top: 5px;
  }

  .active-chat .member-data {
    flex-wrap: wrap;
  }
  .member-data .btn {
    width: 100%;
    padding: 10px 0 0 0;
    margin: 10px 0 0 0;
    justify-content: center;
    border-left: none;
    border-top: 1px solid rgba(194, 206, 221, 0.3);
  }

  .content-wrap .data .data-bg {
    padding: 10px;
  }
  .content-wrap .row {
    margin-right: -15px;
    margin-left: -15px;
  }

  .modal-wrap {
    width: 100%;
    max-width: 100%;
    padding: 0 5px;
  }
  .modal-wrap .go-back {
    padding: 10px 0;
  }
  .modal-wrap .content {
    max-height: calc(100% - 68px);
    padding: 10px 0;
  }
  .modal-wrap .content .data {
    padding: 5px;
  }
  .add-new .item .main-btn {
    width: 100%;
    margin-left: 0;
    margin-top: 15px;
    padding: 21px 29px;
  }

  .modal-info {
    padding: 30px;
  }
  .edit-member {
    margin: 0 -10px -10px -10px;
  }
  .member-fields {
    padding: 0 10px;
  }


  .table-wrap.responsive-table {


    table thead {
      border: none;
      clip: rect(0 0 0 0);
      height: 1px;
      margin: -1px;
      overflow: hidden;
      padding: 0;
      position: absolute;
      width: 1px;
    }

    table tr {
      display: block;
      margin-bottom: 15px;
      border-bottom: 3px solid #F2F2F2;

    }

    table td {
      border-bottom: 1px solid #F2F2F2;

      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 10px 0;

      &::before {
        /*
        * aria-label has no advantage, it won't be read inside a table
        content: attr(aria-label);
        */
        content: attr(data-label);
        padding: 0 10px 0 0;
        text-transform: uppercase;
      }

      &:last-child {
        border-bottom: 0;
      }
    }
  }
  .room-options .add-new .item label.checkbox-option {
    min-width: calc(100% / 2);
  }
}