$Gilroy: 'Gilroy', sans-serif;
$SFPro: 'SF Pro Display', sans-serif;

$MainText: #000000;
$transition: .3s ease-in-out;

:root {
  --white: #ffffff;

}

body {
  position: relative;
  min-width: 320px;
  color: $MainText;
  font-family: $SFPro;
  line-height: 1;
  background: #F5F5F6;
}

.overflow-wrap {
  overflow: hidden;
}

* {
  outline: none;
  word-wrap: break-word;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

a, a:focus, a:hover {
  text-decoration: none;
}

ol, ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

img {
  border: 0;
  max-width: 100%;
  vertical-align: middle;
}

button {
  padding: 0;
  cursor: pointer;
}

input,
button,
textarea {
  border-radius: 0;
  -webkit-appearance: none;
}

input[type=number] {
  -moz-appearance: textfield;

  &::-webkit-outer-spin-button,
  &::-webkit-inner-spin-button {
    /* display: none; <- Crashes Chrome on hover */
    -webkit-appearance: none;
    margin: 0; /* <-- Apparently some margin are still there even though it's hidden */
  }
}

select {
  -webkit-appearance: none;
}

.lazy {
  visibility: hidden;
  opacity: 0;
}

.swiper-lazy-preloader {
  border: 4px solid var(--swiper-preloader-color, #EABD37);
  border-top-color: transparent;
}

.touch-scroll {
  width: 100%;

  .scroll-wrap {
    display: flex;
    flex-wrap: nowrap;
    overflow: hidden;
  }
}

.main-btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 9px 29px;
  font-weight: normal;
  font-size: 12px;
  line-height: 1;
  border-radius: 5px;
  border: 1px solid;
  transition: color $transition, background-color $transition;

  &.gray-blank {
    color: #000000;
    border-color: #F5F5F6;
    background-color: #FFFFFF;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.02);

    &:hover {
      background-color: #F5F5F6;
    }
  }

  &.yellow-blank {
    color: #000000;
    border-color: #EABD37;
    background-color: #FFFFFF;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.02);

    &:hover {
      background-color: #EABD37;
    }
  }

  &.yellow {
    color: #000000;
    border-color: #EABD37;
    background-color: #EABD37;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.02);

    &:hover {
      background-color: #FFFFFF;
    }
  }
}

.login-page {
  background: #FFFFFF;
}

.login-bg {
  width: calc(100% / 2);
  position: relative;

  .bg-wrap {
    width: 50vw;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}

.login-form {
  width: calc(100% / 2);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  padding: 50px 25px;
  position: relative;

  .logo {
    text-align: center;

    a {
      display: inline-block;
    }
  }

  .title {
    padding-bottom: 30px;

    h1 {
      margin: 0;
      font-family: $Gilroy;
      font-weight: 600;
      font-size: 24px;
      line-height: 1.17;
      text-align: center;
      letter-spacing: 0.01em;
      color: #333333;
    }
  }

  form {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    max-width: 430px;
    padding: 15px 0;
    margin: 0 auto;

    label {
      display: block;
      padding: 10px 0;

      input {
        width: 100%;
        padding: 19px 15px;
        font-weight: normal;
        font-size: 15px;

        background: #F5F5F6;
        border: 1px solid #F5F5F6;
        border-radius: 2px;

        &::placeholder {
          color: #79818C;
        }
      }
    }
  }

  .btn {
    padding-top: 29px;

    button {
      width: 100%;
      display: block;
      padding: 19px 15px;
      font-weight: 500;
      font-size: 16px;
      text-align: center;
      background-color: #EABD37;
      border: 1px solid #EABD37;
      border-radius: 5px;
      transition: color $transition, background-color $transition, border-color $transition;

      &:hover {
        color: #EABD37;
        background-color: #F5F5F6;
        border-color: #F5F5F6;
      }
    }

    a {
      display: block;
      padding: 19px 15px;
      margin-top: 13px;
      font-weight: 500;
      font-size: 16px;
      text-align: center;
      color: #79818C;
      transition: color $transition;

      &:hover {
        color: #000000;
      }
    }

    label {
      display: block;
      padding: 19px 15px;
      margin-top: 13px;
      position: relative;
      input[type=checkbox] {
        position: absolute;
        top: 0;
        left: 0;
        opacity: 0;
        visibility: hidden;
        &:checked {
          + span {
            color: #EABD37;
          }
        }
      }
      span {
        display: block;
        font-weight: 500;
        font-size: 16px;
        text-align: center;
        color: #79818C;
        cursor: pointer;
        transition: color $transition;

        &:hover {
          color: #000000;
        }
      }
    }
  }

  .text {
    p {
      margin: 0;
      font-weight: normal;
      font-size: 15px;
      line-height: 1.33;
      text-align: center;
      color: #79818C;
    }
  }
}

.page-intro {
  padding: 8px 0;
}

.page-toolbar {
  background-color: #FFFFFF;

  .title {
    display: none;

    h1 {
      margin: 0;
      display: flex;
      align-items: center;
      font-weight: 500;
      font-size: 14px;
      color: #C2CEDD;

      .icon-dashboard {
        display: inline-block;
        margin-right: 10px;
        font-size: 26px;
      }
    }
  }
}

.toolbar-menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 20px;

  .main-data {
    display: flex;
    align-items: center;
    max-width: 100%;
    padding-right: 15px;
  }

  .open-menu {
    margin-right: 15px;

    button {
      display: block;
      padding: 5px 0;
      font-size: 26px;
      color: #C2CEDD;
      border: none;
      background: none;
      transition: color $transition;

      &:hover {
        color: #EABD37;
      }
    }
  }

  ul {
    display: flex;

    li {
      display: block;
      min-width: 130px;
      position: relative;
      padding: 12px 0;
      font-weight: 500;
      font-size: 14px;
      color: #79818C;
      text-align: center;
      white-space: nowrap;

      a {
        display: inline-block;
        padding: 9px 6px;
        color: inherit;
        transition: color $transition;

        &:hover {
          color: #000000;
        }
      }

      &.active {
        &:after {
          content: '';
          position: absolute;
          left: 0;
          right: 0;
          bottom: 0;
          border-top: 2px solid #C2CEDD;
          pointer-events: none;
        }
      }
    }
  }
}

.main-links {
  ul {
    display: flex;

    li {
      display: block;
      padding-right: 29px;
      font-weight: normal;
      font-size: 11px;
      letter-spacing: 0.01em;
      text-transform: uppercase;
      color: #79818C;

      a {
        display: inline-block;
        padding: 3px 0;
        color: inherit;
        transition: color $transition;
      }
    }
  }
}


.user-data {
  display: flex;
  flex-wrap: wrap;
  align-items: center;

  p {
    margin: 0;
    padding-right: 18px;
    font-weight: normal;
    font-size: 14px;
    letter-spacing: 0.01em;
    text-transform: uppercase;
    color: #79818C;
  }

  .info {
    display: flex;
    align-items: center;
    padding-left: 18px;
    border-left: 1px solid rgba(121, 129, 140, 0.2);

    img {
      width: 30px;
      height: 30px;
      flex-shrink: 0;
      border-radius: 50%;
      border: 1px solid #ffffff;
      overflow: hidden;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    button {
      display: flex;
      flex-shrink: 0;
      padding: 9px 13px;
      margin-left: 20px;
      font-weight: normal;
      font-size: 11px;
      letter-spacing: 0.01em;
      color: #1A1919;
      white-space: nowrap;
      background-color: #FFFFFF;
      border: 1px solid #FFFFFF;
      box-shadow: 0 4px 4px rgba(0, 0, 0, 0.02);
      border-radius: 5px;
      transition: color $transition, background-color $transition, border-color $transition;

      &:hover {
        background-color: #EABD37;
        border-color: #EABD37;
      }
    }
  }
}

.page-menu {
  overflow: hidden;

  .menu-list {
    padding: 12px 21px 0 0;
    min-height: calc(100vh - 48px - 50px);
    position: relative;

    &:before {
      content: '';
      min-height: 120vh;
      width: 30vw;
      background-color: #79818C;
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      z-index: -1;
      pointer-events: none;
    }

    .close-list {
      button {
        display: block;
        padding: 3px 0 0 5px;
        color: #ffffff;
        font-size: 21px;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 3;
        background: none;
        border: none;
      }
    }

    .item {
      padding: 7px 0 21px 0;
      border-top: 1px solid rgba(194, 206, 221, 0.2);

      &:first-child {
        border: none;
      }
    }

    ul {
      li {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 15px 0;
        position: relative;
        font-weight: normal;
        font-size: 15px;
        letter-spacing: 0.01em;
        color: #FFFFFF;

        a {
          color: inherit;
          transition: color $transition;

          &:hover {
            color: #C2CEDD;
          }
        }

        span {
          display: block;
          padding: 2px 8px;
          margin-left: 10px;
          font-weight: 500;
          font-size: 11px;
          background: #FF5260;
          border-radius: 3px;
        }

        &.active {
          &:before {
            content: '';
            position: absolute;
            top: 0;
            left: -17px;
            right: -17px;
            bottom: 0;
            z-index: -1;
            pointer-events: none;
            background-color: rgba(245, 245, 246, 0.2);
            box-shadow: 0 4px 4px rgba(0, 0, 0, 0.02);
            border-radius: 2px;
          }
        }
      }
    }

    p {
      margin: 0;
      padding: 15px 0;
      font-weight: 600;
      font-size: 11px;
      letter-spacing: 0.01em;
      text-transform: uppercase;
      color: #C2CEDD;
    }

    .item-wrap {
      /* width */
      &::-webkit-scrollbar {
        width: 4px;
        height: 4px;
      }

      /* Track */
      &::-webkit-scrollbar-track {
        background: #FFFFFF;
      }

      /* Handle */
      &::-webkit-scrollbar-thumb {
        background: #C2CEDD;
        border-radius: 2px;
      }

      /* Handle on hover */
      &::-webkit-scrollbar-thumb:hover {
        background: #ECBA49;
      }
    }

    .user-data {
      padding-top: 15px;

      .info {
        width: 100%;
        padding: 0 21px;
        justify-content: space-between;
      }
    }
  }

}

.content-wrap {
  padding: 12px 0 0 20px;

  .row {
    margin-right: -8px;
    margin-left: -8px;
    position: relative;
  }

  .add-new .row {
    margin-right: -10px;
    margin-left: -10px;
  }

  .data {
    padding: 8px;
    display: flex;
    flex-direction: column;

    .data-bg {
      width: 100%;
      flex: 1;
      display: flex;
      flex-direction: column;
      padding: 11px 20px;
      background: #FFFFFF;
      box-shadow: 0 4px 4px rgba(0, 0, 0, 0.02);
      border-radius: 2px;
    }

    .data-title {
      display: flex;
      justify-content: space-between;
      padding-bottom: 9px;
      position: relative;
      border-bottom: 1px solid rgba(194, 206, 221, 0.3);

      &.no-border {
        border-color: transparent;
      }

      &.no-padding {
        padding: 0;
      }

      .title {
        padding-right: 15px;

        p {
          display: flex;
          align-items: center;
          margin: 0;
          font-weight: 500;
          font-size: 16px;
          line-height: 1.25;
          color: #000000;

          span {
            display: inline-flex;
            padding: 2px 8px;
            margin-left: 7px;
            font-weight: 500;
            font-size: 11px;
            line-height: 1;
            letter-spacing: 0.01em;
            color: #F5F5F6;
            background: #FF5260;
            border-radius: 3px;
            white-space: nowrap;
          }
        }
      }

      .more-options {
        padding-top: 6px;

        .options-btn {
          p {
            margin: 0;
            font-size: 5px;
            color: #C2CEDD;
          }
        }
      }
    }


  }
}

.data-charts {
  flex: 1;
  display: flex;
  align-items: flex-end;
  padding-bottom: 8px;

  .description {
    max-width: 73px;

    .status {
      p {
        margin: 0;
        display: flex;
        align-items: center;
        font-weight: 500;
        font-size: 28px;
        color: #000000;

        span {
          display: block;
          padding-right: 6px;
          font-size: 18px;
          color: #C2CEDD;
        }
      }
    }

    .name {
      padding-top: 5px;

      p {
        margin: 0;
        font-weight: 600;
        font-size: 11px;
        line-height: 1.27;
        letter-spacing: 0.01em;
        text-transform: uppercase;
        color: #C2CEDD;
      }
    }
  }

  .charts-wrap {
    flex: 1;
  }
}

.user-activity {
  max-height: 336px;
  padding-right: 13px;
  margin-right: -13px;
  overflow: auto;
  /* width */
  &::-webkit-scrollbar {
    width: 4px;
    height: 4px;
  }

  /* Track */
  &::-webkit-scrollbar-track {
    background: #FFFFFF;
  }

  /* Handle */
  &::-webkit-scrollbar-thumb {
    background: #C2CEDD;
    border-radius: 2px;
  }

  /* Handle on hover */
  &::-webkit-scrollbar-thumb:hover {
    background: #ECBA49;
  }

  ul {
    li {
      display: flex;
      padding: 17px 0;
      border-bottom: 1px solid rgba(194, 206, 221, 0.3);

      .img-wrap {
        width: 37px;
        height: 37px;
        flex-shrink: 0;
        border-radius: 50%;
        overflow: hidden;

        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }

      .data-wrap {
        //width: calc(100% - 37px);
        flex: 1;
        padding-left: 12px;

        .title {
          p {
            display: flex;
            align-items: center;
            margin: 0;
            font-weight: 600;
            font-size: 14px;
            letter-spacing: -0.41px;
            color: #333333;

            span {
              display: inline-flex;
              padding: 3px 8px;
              margin-left: 6px;
              font-weight: 500;
              font-size: 11px;
              line-height: 1;
              letter-spacing: 0.01em;
              color: #F5F5F6;
              background: #EABD37;
              border-radius: 3px;
              white-space: nowrap;
            }
          }
        }

        .action {
          padding-top: 3px;

          p {
            margin: 0;
            font-weight: 500;
            font-size: 14px;
            line-height: 1.29;
            letter-spacing: -0.41px;
            color: #333333;
          }
        }

        .text {
          p {
            margin: 0;
            font-weight: normal;
            font-size: 14px;
            line-height: 1.29;
            letter-spacing: -0.41px;
            color: #333333;
          }
        }
      }

      .post-time {
        display: none;
        padding: 9px 0 0 15px;

        p {
          margin: 0;
          font-weight: 500;
          font-size: 14px;
          color: #79818C;
          white-space: nowrap;
        }
      }
    }
  }

  &.today {
    ul li .data-wrap .title p {
      color: #79818C;
    }
  }
}

.slider-control {
  .arrows {
    display: flex;

    .arrow-icon {
      padding: 2px;

      span {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 22px;
        height: 22px;
        font-size: 11px;
        color: #79818C;
        background-color: #F5F5F5;
        border-radius: 4px;
        cursor: pointer;
        transition: color $transition, background-color $transition;

        &:hover {
          color: #FFFFFF;
          background-color: #EABD37;
        }
      }

      &.disabled {
        opacity: .8;
        pointer-events: none;
      }
    }
  }
}

.reviews-slider {
  .slider-control {
    display: flex;
    justify-content: flex-end;
  }
}

.review-item {
  .data-wrap {
    display: flex;
  }

  .img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    border: 1px solid #EABD37;
    overflow: hidden;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  .info {
    flex: 1;
    padding-left: 9px;

    .name {
      p {
        margin: 0;
        font-weight: 600;
        font-size: 14px;
        line-height: 1.57;
        letter-spacing: -0.41px;
        color: #333333;
      }
    }
  }

  .text-wrap {
    padding-top: 10px;

    p {
      margin: 0;
      max-width: 265px;
      font-weight: normal;
      font-size: 14px;
      line-height: 1.29;
      letter-spacing: -0.41px;
      color: #333333;
    }
  }
}

.rating {
  padding: 5px 0 0 2px;

  p {
    display: flex;
    margin: 0;

    span {
      display: block;
      padding-right: 2px;
      font-size: 13px;

      &.icon-star {
        color: #EABD37;
      }

      &.icon-empty {
        color: #79818C;
      }

      &:last-child {
        padding-right: 0;
      }
    }
  }
}

.set-rating {
  p {
    display: flex;
    flex-direction: row-reverse;
    justify-content: center;
    margin: 0;

    span {
      display: block;
      padding-right: 2px;
      font-size: 13px;
      transition: color $transition;

      &.icon-empty {
        color: #79818C;

        &.active:before,
        &.active ~ span.icon-empty:before,
        &:hover:before,
        &:hover ~ span.icon-empty:before {
          content: "\e904";
          color: #EABD37;
        }
      }

      &:last-child {
        padding-right: 0;
      }
    }
  }
}


.table-intro {
  display: flex;
  padding-bottom: 19px;


  .search-field {
    flex: 1;
  }

  .total {
    padding-left: 20px;
    display: flex;

    p {
      flex: 1;
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      margin: 0;
      padding: 15px 35px;
      font-weight: normal;
      font-size: 14px;
      letter-spacing: 0.01em;
      color: #79818C;
      background: #FFFFFF;
      box-shadow: 0 4px 4px rgba(0, 0, 0, 0.02);
      border-radius: 2px;

      span {
        display: inline-block;
        margin-left: 9px;
        font-weight: 500;
        font-size: 20px;
        color: #000000;
        white-space: nowrap;
      }
    }
  }

  .select-date input {
    max-width: 130px;
    min-width: 127px;
  }

}

.toolbar-options {
  display: flex;
  align-items: center;
  padding: 10px 0;

  .item {
    padding-left: 20px;

    &:first-child {
      padding-left: 0;
    }

    &.left-border {
      margin-left: 20px;
      border-left: 1px solid rgba(121, 129, 140, 0.2);
    }

    .main-btn {
      &.gray-blank {
        padding: 9px 14px;
        font-size: 11px;
      }
    }
  }

  .select-field select {
    max-width: 155px;
    min-width: 116px;
  }

  .select-date input {
    max-width: 130px;
    min-width: 127px;
  }
}

.select-field {
  display: flex;
  align-items: center;
  position: relative;

  span.name {
    display: block;
    padding-right: 17px;
    font-weight: normal;
    font-size: 12px;
    letter-spacing: 0.01em;
    color: #79818C;
  }

  select {
    flex: 1;
    padding: 8px 25px 8px 14px;
    font-weight: normal;
    font-size: 12px;
    letter-spacing: 0.01em;
    text-transform: uppercase;
    color: #79818C;
    border: 1px solid #C2CEDD;
    box-sizing: border-box;
    border-radius: 5px;
  }

  &:after {
    font-family: 'icoFont', serif;
    content: "\e908";
    display: flex;
    align-items: center;
    font-size: 7px;
    color: #79818C;
    position: absolute;
    top: 0;
    right: 12px;
    bottom: 0;
  }
}

.select-date {
  display: flex;
  align-items: center;
  position: relative;

  span.name {
    display: block;
    padding-right: 17px;
    font-weight: normal;
    font-size: 12px;
    letter-spacing: 0.01em;
    color: #79818C;
  }

  input {
    flex: 1;
    padding: 8px 32px 8px 14px;
    font-weight: normal;
    font-size: 12px;
    letter-spacing: 0.01em;
    text-transform: uppercase;
    color: #79818C;
    border: 1px solid #C2CEDD;
    box-sizing: border-box;
    border-radius: 5px;
  }

