        border-radius: 2px;
      }

      /* Handle on hover */
      &::-webkit-scrollbar-thumb:hover {
        background: #ECBA49;
      }
    }

    .chat-wrap {
      overflow: hidden;
    }
  }

  .chat-control {
    display: flex;
    align-items: center;
    padding: 10px 15px;
    background: #FAFAFA;
    border: 1px solid #C2CEDD;
    box-sizing: border-box;
    border-radius: 10px 10px 10px 0;

    label {
      display: block;
      flex: 1;

      textarea {
        width: 100%;
        height: 60px;
        padding: 0 6px 0 0;
        font-weight: normal;
        font-size: 14px;
        line-height: 1.29;
        letter-spacing: -0.41px;
        color: #333333;
        resize: none;
        border: none;
        background: none;

        &::placeholder {
          color: #79818C;
        }

        /* width */
        &::-webkit-scrollbar {
          width: 4px;
          height: 4px;
        }

        /* Track */
        &::-webkit-scrollbar-track {
          background: #FFFFFF;
        }

        /* Handle */
        &::-webkit-scrollbar-thumb {
          background: #C2CEDD;
          border-radius: 2px;
        }

        /* Handle on hover */
        &::-webkit-scrollbar-thumb:hover {
          background: #ECBA49;
        }
      }
    }

    .btn {
      display: flex;

      button {
        display: flex;
        margin-left: 15px;
        font-size: 28px;
        background: none;
        border: none;

        &:first-child {
          margin-left: 0;
        }

        label {
          display: block;
          position: relative;

          input[type=file] {
            position: absolute;
            top: 0;
            left: 0;
            opacity: 0;
            visibility: hidden;
          }
        }

        span {
          display: block;
          border-radius: 50%;

          &.icon-smile {
            color: rgba(112, 124, 151, 0.5);
            transition: color $transition;

            &:hover {
              color: #1F60DD;
            }
          }

          &.icon-file,
          &.icon-send {
            color: #FFFFFF;
            background: #1F60DD;
            transition: color $transition, background $transition;

            &:hover {
              color: #1F60DD;
              background: #FFFFFF;
            }
          }
        }
      }
    }
  }
}

.message-text {
  display: flex;
  align-items: flex-end;
  padding: 10px 0;
  opacity: 0;

  .content {
    max-width: 50%;
    min-width: 266px;
    padding: 20px 11px 10px 15px;
    position: relative;

    &:before {
      content: '';
      width: 7px;
      height: 13px;
      display: block;
      position: absolute;
      background-repeat: no-repeat;
      background-size: contain;
      background-position: left center;
      pointer-events: none;
    }

    .name {
      p {
        margin: 0;
        font-weight: 500;
        font-size: 15px;
        line-height: 1.33;
        letter-spacing: -0.00615385em;
      }
    }

    .text {
      p {
        margin: 0;
        font-weight: normal;
        font-size: 15px;
        line-height: 1.33;
        letter-spacing: -0.00615385em;
      }
    }

    .status {
      padding-top: 18px;

      p {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        margin: 0;
        font-weight: normal;
        font-size: 10px;
        letter-spacing: -0.00615385em;

        span.icon-seen {
          display: block;
          padding-left: 4px;
          font-size: 6px;
        }
      }
    }

    .file {
      display: flex;
      align-items: center;
      padding-top: 7px;

      span.icon {
        width: 51px;
        height: 51px;
        display: flex;
        flex-shrink: 0;
        align-items: center;
        justify-content: center;
        border-radius: 50%;

        span.icon-document {
          font-size: 24px;
        }
      }

      span.info {
        padding-left: 12px;

        span.text {
          display: block;
          font-weight: normal;
          font-size: 14px;
          letter-spacing: -0.00615385em;

          span.weight {
            display: block;
            padding-top: 9px;
            font-size: 13px;
          }
        }
      }
    }
  }

  .user {
    width: 20px;
    height: 20px;
    flex-shrink: 0;
    border-radius: 50%;
    overflow: hidden;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &.sent {
    justify-content: flex-start;

    animation: slideright .8s cubic-bezier(0.785, 0.135, 0.15, 0.86) both;

    .content {
      order: 2;
      margin-left: 15px;
      border-radius: 15px 15px 15px 10px;
      background: #F0F1F2;

      &:before {
        left: -6px;
        bottom: 8px;
        background-image: url(../images/message-arrow-s.svg);
        transform: scale(-1, 1);
      }

      .name {
        p {
          color: #06253B;
        }
      }

      .text {
        p {
          color: #454545;
        }
      }

      .status {
        p {
          color: #BCC2C7;

          span.icon-seen {
            color: #454545;
          }
        }
      }

      .file {
        span.icon {
          background: #BCC2C7;

          span.icon-document {
            color: #454545;
          }
        }

        span.info {
          span.text {
            color: #454545;

            span.weight {
              color: #BCC2C7;
            }
          }
        }
      }
    }

    .user {
      order: 1;
      display: none;
    }
  }

  &.received {
    justify-content: flex-end;
    animation: slideleft .8s cubic-bezier(0.785, 0.135, 0.15, 0.86) both;

    .content {
      order: 1;
      margin-right: 15px;
      border-radius: 15px 15px 10px 15px;
      background: #1F60DD;

      &:before {
        right: -6px;
        bottom: 8px;
        background-image: url(../images/message-arrow-r.svg);
      }

      .name p,
      .text p {
        color: #FFFFFF;
      }

      .status {
        p {
          color: rgba(255, 255, 255, 0.4);

          span.icon-seen {
            color: #FFFFFF;
          }
        }
      }

      .file {
        span.icon {
          background: rgba(255, 255, 255, 0.4);

          span.icon-document {
            color: #FFFFFF;
          }
        }

        span.info {
          span.text {
            color: #FFFFFF;

            span.weight {
              color: rgba(255, 255, 255, 0.4);
            }
          }
        }
      }
    }

    .user {
      order: 2;
    }
  }
}

@keyframes slideright {
  from {
    opacity: 0;
    transform: translateX(-30%);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes slideleft {
  from {
    opacity: 0;
    transform: translateX(30%);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

